*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-size: 13px;
  color: #ffffff;
}

.button {
  text-align: center;
  border: none;
  text-transform: uppercase;
  display: block;
  padding: 15px 31px;
  cursor: pointer;
}

